<style>
  .mh-address {
    color: #666;
    font-size: 12px;
  }
</style>

<template>
  <div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary" @click="goAddPage" size="mini" icon="el-icon-plus">添加门店配送</el-button>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <el-form-item label="门店">
            <el-select class="search-input" v-model="searchContent.storeNo" filterable placeholder="请选择门店" clearable size="small" @change="doSearchHandel(true)">
              <el-option
                v-for="item in storeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="配送公司">
            <el-select class="search-input" v-model="searchContent.logisticsCompanyId" placeholder="请选择配送公司" size="small" @change="doSearchHandel(true)">
              <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in logisticsCompanyOptList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="立即送时间">
            <el-time-picker
              v-model="searchContent.dcOpenTime"
              format="HH:mm:ss"
              value-format="HH:mm:ss"
              class="search-input-small-width"
              size="small" 
              clearable
              @change="doSearchHandel(true)"
              placeholder="开始时间">
            </el-time-picker>
            -
            <el-time-picker
              v-model="searchContent.dcCloseTime"
              format="HH:mm:ss"
              class="search-input-small-width"
              value-format="HH:mm:ss"
              size="small" 
              clearable
              @change="doSearchHandel(true)"
              placeholder="结束时间">
            </el-time-picker>
          </el-form-item>
          
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <!-- 搜索结果 -->
    <div class="table-wraper">
      <div class="batch-handel-w">
        <el-button class="sup-mini" size="mini" icon="el-icon-edit" :disabled="checkedList.length === 0" @click="batchUpdateSort">修改优先级</el-button>
        <el-button class="sup-mini" size="mini" icon="el-icon-edit" :disabled="checkedList.length === 0"  @click="batchUpdateDcTime">修改立即送时间</el-button>
      </div>
      <el-table
        class="fixed-height"
        ref="multipleTable"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        @select="handleSelect"
        @select-all="handleSelectAll"
        size="small">
        <el-table-column
          type="selection"
          align="center">
        </el-table-column>
        <el-table-column label="门店信息" min-width="200px">
          <template slot-scope="scope">
            <div>
              <p>
                {{scope.row.storeNo}}-{{scope.row.storeName}}
              </p>
              <p class="mh-address">{{scope.row.storeAddr}}</p>
            </div>
          </template>  
        </el-table-column>
        <el-table-column prop="logisticsCompanyId" label="配送公司" min-width="120px" :formatter="logisticsCompanyIdFormater"></el-table-column>
        <el-table-column prop="sortNum" label="派单优先级" min-width="120px"></el-table-column>
        <el-table-column label="立即送时间" min-width="140px">
          <template slot-scope="scope">
            <div>
              {{scope.row.dcOpenTime || '-'}}-{{scope.row.dcCloseTime || '-'}}
            </div>
          </template>  
        </el-table-column>
        <el-table-column
          align="center"
          fixed="right"
          label="操作"
          width="80">
          <template slot-scope="scope" size="small">
            <div>  
              <el-button @click="editItem(scope.row)" type="text" size="mini" icon="el-icon-edit">编辑</el-button>
            </div>
            <div>
              <el-button @click="deleteItem(scope.row)" type="text" size="mini" icon="el-icon-delete">删除</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="[10, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
    
    <!-- 批量修改优先级 -->
    <el-dialog
      title="批量修改优先级"
      :visible.sync="sortNumDialogVisible"
      width="440px">
      <div>
        <el-form>
          <el-form ref="form" label-width="100px">
            <el-form-item label="派单优先级" required>
              <el-select class="search-date-picker" v-model="sortNum" placeholder="请选择派单优先级" size="small" clearable>
                <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in sortNumOptList"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </el-form>
        <div>
          <p>注意：</p>
          <p>1、请选择同一配送公司的不同门店批量设置；</p>
          <p>2、选择优先级保存后，会批量将门店其他配送公司优先级相应调整。</p>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="sortNumDialogVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="batchUpdateSortHandel" size="mini" :disabled="idDisabled" :loading="idDisabled">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="批量修改立即送时间"
      :visible.sync="dcTimeDialogVisible"
      width="440px">
      <div>
        <el-form>
          <el-form ref="form" label-width="100px">
            <el-form-item label="立即送时间" required>
              <el-time-picker
                v-model="dcOpenTime"
                format="HH:mm:ss"
                value-format="HH:mm:ss"
                class="search-input-small-width"
                size="small" 
                clearable
                placeholder="最早时间">
              </el-time-picker>
              -
              <el-time-picker
                v-model="dcCloseTime"
                format="HH:mm:ss"
                class="search-input-small-width"
                value-format="HH:mm:ss"
                size="small" 
                clearable
                placeholder="最晚时间">
              </el-time-picker>
            </el-form-item> 
          </el-form>
        </el-form>
        <div>
          <p>注意：</p>
          <p>1、请选择同一配送公司的不同门店批量设置；</p>
          <p>2、设置保存后，会批量将门店的配送公司立即送时间改为当前时间。</p>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dcTimeDialogVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="batchUpdateDcTimeHandel" size="mini" :disabled="idDisabled" :loading="idDisabled">确 定</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  const reqUrl = {
    // 获取门店列表接口
    search: '/plateform/a/store/condition/search',
    // 门店配置列表
    listPage: '/plateform/a/mall/storeLogistics/listPage',
    // 门店配置批量删除
    batchUpdateDel: '/plateform/a/mall/storeLogistics/batchUpdateDel',
    // 门店配置批量排序
    batchUpdateSort: '/plateform/a/mall/storeLogistics/batchUpdateSort',
    // 门店配置批量修改送达时间
    batchUpdateDcTime: '/plateform/a/mall/storeLogistics/batchUpdateDcTime'
  }
  export default {
    data() {
      return {
        idDisabled: false,
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          storeNo: '', // 门店编号
          dcOpenTime: '', // 配送开始时间 格式化 HH:mm:ss
          dcCloseTime: '', // 配送结束时间 格式化 HH:mm:ss
          logisticsCompanyId: '' // 配送公司ID 1001-门店自配送 1002-美团
        },
        tableData: [],
        // 选中的门店 供批量操作使用
        checkedList: [],
        // 门店列表
        storeOptions: [],
        logisticsCompanyOptList: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '门店自配送',
            value: 1001
          },
          {
            label: '美团配送',
            value: 1002
          },
          {
            label: '达达配送',
            value: 1003
          },
          {
            label: '顺丰同城配送',
            value: 1004
          }
        ],
        // 批量修改优先级
        sortNumDialogVisible: false,
        sortNum: '', // 绑定的值
        sortNumOptList: [
          {
            label: '1',
            value: 1
          },
          {
            label: '2',
            value: 2
          },
          {
            label: '3',
            value: 3
          }
        ],
        // 批量修改立即送时间
        dcTimeDialogVisible: false,
        dcOpenTime: '', // 配送开始时间 格式化 HH:mm:ss
        dcCloseTime: '' // 配送结束时间 格式化 HH:mm:ss

      }
    },
    created() {
      // 根据用户选择的城市获取门店
      this.getStoreList()
    },
    mounted() {
      let searchContent = Storage.get("searchContentBlxDelivery")
      if(this.$route.meta.keepAlive && searchContent) {
        this.pageNum = searchContent.pageNum
        this.searchContent = searchContent.searchContent
      }
      // 根据用户选择的城市获取数据
      this.doSearchHandel()
      Storage.remove('searchContentBlxDelivery')
    },
    methods: {
      /**
       * [goAddPage 添加门店配送]
       * @return {[type]} [description]
       */
      goAddPage() {
        this.$router.push('/blx_delivery/add')
      },
      /**
       * [getStoreList 获取门店列表]
       * @return {[type]} [description]
       */
      getStoreList() {
        this.$http.post(reqUrl.search).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              let optionsArr = []
              for(let item of res.data) {
                let optionItem = {
                  value: item.storeNo,
                  label: item.storeNo +'-'+ item.storeName
                }
                optionsArr.push(optionItem)
              }

              this.storeOptions = optionsArr
            }else {
              this.storeOptions = []
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取门店列表):", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel 查询列表接口]
       * @param  {[type]} _param [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        this.checkedList = []
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        } 
        this.$http.post(reqUrl.listPage, {...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data && res.data.result && res.data.result.length) {
              for(let item of res.data.result) {
                item.uniqueId = `${item.id}${item.storeNo}`
              }
              this.tableData = res.data.result
            }else {
              this.tableData = []
            }
            this.total = res.data.total
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示(配送管理):',
            message: e.message
          })
        })
      },
      logisticsCompanyIdFormater(row, column, cellValue, index) {
        if(cellValue == 1001) {
          return '门店自配送'
        }else if(cellValue == 1002) {
          return '美团配送'
        }else if(cellValue == 1003) {
          return '达达配送'
        }else if(cellValue == 1004) {
          return '顺丰同城配送'
        }else {
          return '--'
        }
      },
      /**
       * [editItem 编辑]
       * @param  {[type]} _row [行信息]
       * @return {[type]}      [description]
       */
      editItem(_row) {
        // 设置搜索条件
        let _paramMesg = {
          pageNum: this.pageNum,
          searchContent: this.searchContent
        }
        Storage.set("searchContentBlxDelivery", _paramMesg)
        this.$router.push('/blx_delivery/edit/' + `${_row.id}/${_row.storeNo}`)
      },
      /**
       * [deleteItem 删除]
       * @param  {[type]} _row [行信息]
       * @return {[type]}      [description]
       */
      deleteItem(_row) {
        let ids = []
        ids.push(_row.id)
        this.$confirm('您确定要删除所选门店的配送公司吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(reqUrl.batchUpdateDel, {ids: ids, isDeleted: 1}).then(res => {
            if(res.code === 0) {
              this.$message.success("删除成功")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArray(row.uniqueId, this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('uniqueId', row.uniqueId, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.uniqueId] ? '' : hash[next.uniqueId] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {        
          // 全选
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.uniqueId] ? '' : hash[next.uniqueId] = true && item.push(next)
            return item
          }, [])
        }else { 
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'uniqueId')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [batchUpdateSort 批量修改优先级]
       * @return {[type]} [description]
       */
      batchUpdateSort() {
        let _logisticsCompanyIds = []
        for(let item of this.checkedList) {
          _logisticsCompanyIds.push(item.logisticsCompanyId)
        }
        let _logisticsCompanyIdsT = Array.from(new Set(_logisticsCompanyIds))
        if(_logisticsCompanyIdsT.length === 1) {
          this.sortNumDialogVisible = true
        }else {
          this.$notify.warning({
            title: '温馨提示',
            message: '请选择同一配送公司的不同门店批量设置'
          })
          return
        }
      },
      /**
       * [batchUpdateSortHandel 确定修改优先级]
       * @return {[type]} [description]
       */
      batchUpdateSortHandel() {
        if(!this.sortNum) {
          this.$message("请选择派单优先级")
          return
        }
        let _ids = []
        for(let item of this.checkedList) {
          _ids.push(item.id)
        }
        this.idDisabled = true
        this.$http.post(reqUrl.batchUpdateSort, {ids: _ids, sortNum: this.sortNum}).then(res => {
          setTimeout(() => {
            this.idDisabled = false
          }, 800)
          if(res.code === 0) {
            this.doSearchHandel()
            this.sortNumDialogVisible = false
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.idDisabled = false
          this.$notify.error({
            title: '提示:',
            message: e.message
          })
        })
      },
      /**
       * [batchUpdateDcTime 批量我需改时间]
       * @return {[type]} [description]
       */
      batchUpdateDcTime() {
        let _logisticsCompanyIds = []
        for(let item of this.checkedList) {
          _logisticsCompanyIds.push(item.logisticsCompanyId)
        }
        let _logisticsCompanyIdsT = Array.from(new Set(_logisticsCompanyIds))
        if(_logisticsCompanyIdsT.length === 1) {
          this.dcTimeDialogVisible = true
        }else {
          this.$notify.warning({
            title: '温馨提示',
            message: '请选择同一配送公司的不同门店批量设置'
          })
          return
        }
      },
      /**
       * [batchUpdateDcTimeHandel 批量修改立即送时间]
       * @return {[type]} [description]
       */
      batchUpdateDcTimeHandel() {
        if(!this.dcOpenTime || !this.dcCloseTime) {
          this.$message("请选择立即送时间")
          return
        }
        if(utils.changeStrToMinutes(this.dcCloseTime) <= utils.changeStrToMinutes(this.dcOpenTime)) {
          this.$message("最晚可接单时间不能早于最早可接单时间")
          return
        }
        let _ids = []
        for(let item of this.checkedList) {
          _ids.push(item.id)
        }
        this.idDisabled = true
        this.$http.post(reqUrl.batchUpdateDcTime, {ids: _ids, dcOpenTime: this.dcOpenTime, dcCloseTime: this.dcCloseTime}).then(res => {
          setTimeout(() => {
            this.idDisabled = false
          }, 800)
          if(res.code === 0) {
            this.doSearchHandel()
            this.dcTimeDialogVisible = false
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.idDisabled = false
          this.$notify.error({
            title: '提示:',
            message: e.message
          })
        })
      }

    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      from.meta.keepAlive = false;
      next();
    }
    
  }
</script>



